.statistics {
  width: 100%;
  height: auto;

  &__container{
    height: 386px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../images/statisticsBackground.jpg");
  }
  &__block{
    width: 33.3%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    &--count{
      font-size: 90px;
      font-weight: 200;
      margin-bottom: 30px;
    }
    &--title{
      font-size: 18px;
      text-transform: uppercase;
      margin-bottom: 30px;
    }
    &--text{
      font-size: 16px;
      width: 70%;
      align-self: center;
      text-align: center;
      margin-bottom: 30px;
    }
  }
}